.adm-picker {
  width: 100%;
  height: 300px;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
}
.adm-picker-header {
  flex: none;
  border-bottom: solid 1px var(--adm-border-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 6px 8px;
}
.adm-picker-header-button {
  font-size: 15px;
  display: inline-block;
  padding: 4px 4px;
}
.adm-picker-header-title {
  padding: 4px 4px;
  font-size: 15px;
  color: var(--adm-color-text);
  text-align: center;
  flex: 1;
}
.adm-picker-columns {
  height: 100%;
  width: 100%;
  display: flex;
  flex: auto;
  position: relative;
  overflow: hidden;
}
.adm-picker-column {
  height: 100%;
  flex: 1;
  user-select: none;
  touch-action: none;
  position: relative;
}
.adm-picker-column-wheel {
  width: 100%;
  cursor: grab;
  position: absolute;
  top: calc(50% - 17px);
  left: 0;
}
.adm-picker-column-wheel::before {
  content: ' ';
  display: block;
  position: absolute;
  width: 100%;
  height: 100vh;
  top: -100vh;
}
.adm-picker-column-wheel::after {
  content: ' ';
  display: block;
  position: absolute;
  width: 100%;
  height: 100vh;
  bottom: -100vh;
}
.adm-picker-column-item {
  font-size: 16px;
  padding: 0 6px;
  height: 34px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.adm-picker-column-item-label {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.adm-picker-mask {
  position: absolute;
  left: 0;
  width: 100%;
  height: calc(50% - 17px);
  pointer-events: none;
}
.adm-picker-mask-top {
  top: 0;
  border-bottom: solid 1px var(--adm-border-color);
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.7) 50%, #ffffff);
}
.adm-picker-mask-bottom {
  bottom: 0;
  border-top: solid 1px var(--adm-border-color);
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.7) 50%, #ffffff);
}
